*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.NavLink{
  text-decoration: none;
}

.marginTop{
  margin-top: 20px;
}

.body{
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.main{
  width: 100%;
  height: 100vh;
  background-color: #F6F6F6;


}

.mainContainer{
  margin: 0 auto;
  width: 60%;
  max-width: 1142px;
  height: 100%;
  display: flex;
}

.mainLeft{
  width: 68%;
  height: 100%;
}

.mainRight{
  width: 32%;
  height: 100%;
  display: flex;
  flex-direction: column;
}